/* Navbar */
.navbar {
  background-color: #1f1f2f;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Logo */
.logo {
  font-size: 1.3rem;
  font-weight: bold;
  color: #fff;
  display: flex;
  align-items: center;
}

.logoImg {
  height: 40px;
  margin-right: 10px;
}

/* Search Bar */
.searchForm {
  width: 50%;
  max-width: 400px;
}

.searchInput {
  border-radius: 5px 0 0 5px;
  border: 1px solid #555;
  background-color: #2c2c3a;
  color: #fff;
}

.searchInput::placeholder {
  color: #aaa;
}

.searchButton {
  border: none;
  background-color: #444;
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.searchButton:hover {
  background-color: #17a2b8;
}

/* Icons */
.navIcon {
  color: #fff;
  font-size: 1.3rem;
  margin-left: 20px;
  position: relative;
}

.navIcon:hover {
  color: #17a2b8;
}

/* Notification Badge */
.badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #dc3545;
  color: #fff;
  font-size: 0.75rem;
  padding: 2px 6px;
  border-radius: 50%;
}

/* Profile Dropdown */
.profileToggle {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.profileIcon {
  font-size: 1.5rem;
  color: #fff;
}

.profileIcon:hover {
  color: #17a2b8;
}
